// @mui
import { useEffect, useState } from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// components
import { useSettingsContext } from 'src/components/settings';
import DataTable from 'react-data-table-component';
import { Button, IconButton, InputAdornment, Menu, MenuItem, TextField } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import { decryptToken, color } from 'src/utils/common';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { SingleInputDateRangeField } from '@mui/x-date-pickers-pro/SingleInputDateRangeField';
import dayjs from 'dayjs';
import { LocalizationProvider } from '@mui/x-date-pickers';
// ----------------------------------------------------------------------

export default function ReceiptView() {
  const settings = useSettingsContext();
  const { enqueueSnackbar } = useSnackbar();
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [value, setValue] = useState(() => [
    dayjs('2022-04-17'),
    dayjs('2022-04-21'),
  ]);

  const [data, setData] = useState([]);
  // For Actions Dropdown
  const [anchorEl, setAnchorEl] = useState(null);
  const openMenu = Boolean(anchorEl);
  // Temporary State
  const [tempData, setTempData] = useState({});
  const [openDialog, setOpenDialog] = useState(false);


  const fetchData = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.receipt.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setIsLoading(false);
        console.log(response.data.data);
        setData(response.data.data);
      };

    } catch (err) {
      enqueueSnackbar('Failed to fetch records!', { variant: 'error' });
      setIsLoading(false);
    }
  };

  useEffect(() => {
    setIsLoading(true);
    fetchData();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const columns = [
    {
      name: 'Customer Name',
      selector: (row) => row.customerName,
      sortable: true,
    },
    {
      name: 'Invoice Number',
      selector: (row) => row.invoiceNumber,
      sortable: true,
    },
    {
      name: 'Amount Paid',
      selector: (row) => row.amount,
      sortable: true,
    },
    {
      name: 'Payment Mode',
      selector: (row) => row.paymentId.paymentMode,
      sortable: true,
    },
    {
      name: 'Payment Status',
      selector: (row) => row.paymentId.paymentStatus,
      sortable: true,
    },
    {
      name: 'Party Type',
      selector: (row) => row.partyType,
      sortable: true,
    },
    {
      name: 'Payment Date',
      selector: (row) => new Date(row.createdAt).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
      sortable: true,
    },
    {
      name: 'Actions',
      width: "130px",
      cell: (row) => (
        <>
          <Button onClick={(event) => {
            setAnchorEl(event.currentTarget);
            setTempData(row)
          }} style={{ color: color.actionButton }} variant='outlined' endIcon={<ArrowDropDownIcon />}>Action</Button>
          <Menu
            id="basic-menu"
            anchorEl={anchorEl}
            open={openMenu}
            onClose={() => {
              setAnchorEl(null);
              setTempData({});
            }}
            MenuListProps={{
              'aria-labelledby': 'basic-button',
            }}
          >
            <MenuItem onClick={() => {
              setOpenDialog(true);
              setAnchorEl(null);
            }}>Delete</MenuItem>

          </Menu>
        </>
      ),
    },
  ];

  const filteredRecords = data.filter(filteredData => {
    const invoiceNumberMatches = filteredData?.invoiceNumber?.toLowerCase().includes(searchQuery?.toLowerCase());
    const customerNameMatches = filteredData?.customerName?.toLowerCase().includes(searchQuery?.toLowerCase()); // Assuming 'customerName' is the key for the customer name in your data
    return invoiceNumberMatches || customerNameMatches;
  });



  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <Typography variant="h4"> Receipts </Typography>
      <div style={{ display: 'flex', justifyContent: 'start', alignItems: 'center', marginTop: '20px', gap: '10px' }}>
        <TextField
          size="small"
          onChange={(e) => setSearchQuery(e.target.value)}
          type="text"
          placeholder="Search by Inv/Name"
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SearchIcon />
              </InputAdornment>
            ),
          }}
        />
        <LocalizationProvider dateAdapter={AdapterDayjs} >
          <SingleInputDateRangeField
            size='small'
            label="Date Range"
            value={value}
            onChange={(newValue) => setValue(newValue)}
          />
        </LocalizationProvider>
      </div>
      <div style={{ marginTop: "20px" }}>
        <DataTable
          columns={columns}
          data={filteredRecords}
          pagination
          progressPending={isLoading}
        />
      </div>

    </Container>
  );
}
